<template>
  <section>
    <!--.wrap = container (width: 90%) -->
    <div class="wrap">
      <div class="grid vertical-align">
        <div class="column">
          <h3>
            <i class="fas fa-code-branch"></i>
            &nbsp;
            <strong>对象池</strong></h3>
          <p class="text-intro">场景中的每一个物体都是一个节点</p>
          <p>
            以预制体为模版，初始化场景中最大可能同时存在的节点数。
            在游戏运行时，进行调用激活与回收。
            <br>
            而非需要时再创建，提高运行效率。
          </p>
        </div>
        <!-- .end .column -->
        <div class="column">
          <pre>
              init () {
                  this.NodePool = new cc.NodePool()
                  for ( let i = 0; i &lt; this.size; i++ ) {
                      let obj = cc.instantiate(this.prefab)
                      this.NodePool.put(obj)
                  }
              },

              get ()  {
                  return this.NodePool.get()
              },

              put (obj) {
                  return this.NodePool.put(obj)
              },
          </pre>
        </div>
        <!-- .end .column -->
      </div>
      <!-- .end .grid -->
    </div>
    <!-- .end .wrap -->
  </section>
</template>

<script>
export default {

}
</script>

